.App {
  text-align: center;
}

body, html {
  overflow-x: hidden;
  margin: 0;
  padding: 0;
  font-family: ivyepic-variable, sans-serif;
  text-align: justify;
  hyphens: manual;

  scroll-behavior: smooth;
}


* {
  box-sizing: border-box;
}

section {
  padding: 40px 0;
}

h1, h2, h3, h4, h5, h6 {
  font-variation-settings: "slnt" 0, "wdth" 100, "wght" 600;
  hyphens: manual;
}

h1 {
  font-size: 2rem;
}

h2 {
  padding-top: 20px;
}


.section-header {
  text-align: left;
  font-size: 2rem;
  padding: 0;
}

.content-header {
  text-align: left;
}


.title-wrapper {
  padding: 85px 10px 0;
  max-width: 100vw;
}

.title-section {
  margin-top: 20px;
  text-align: center;

  h3 {
    font-size: 1.4rem;
  }
}

.title {
  /* color: #333; */
  font-size: 2.5rem;
  margin-bottom: 20px;
}


.image-container {
  width: 100%;
  overflow: hidden;
  text-align: center;
}

img {
  max-width: 100%;
  display: block;
}

.image-container img {
  margin: 0 auto;
}

.logo-large {
  width: 30%;
  max-width: 800px;
  min-width: 300px;
  margin: auto;
}

.content-wrapper {
  overflow-y: auto;
  width: 70%;
  max-width: 1300px;
  margin: 0 auto 30px;
  padding: 0 20px;
}

.paragraph {
  font-size: 1.1rem;

  /* color: #555; */
  /* color: whitesmoke; */
}

.boldText {
  font-variation-settings: "slnt" 0, "wdth" 100, "wght" 500;
}

.source {
  font-size: 0.8rem;
  color: #666;
  text-align: end;
}

.hint {
  font-size: 0.9rem;
}

@media (max-width: 1300px) {
  .content-wrapper {
    /* width: calc(100% - 40px); Full width minus 20px margins on each side */
    width: 85%;
  }
}

@media (max-width: 1200px) {
  .title-wrapper {
    padding: 64px 10px 0;
  }
}

@media (max-width: 768px) {
  body, html {
    text-align: left;
    hyphens: none;
  }
  
  .content-wrapper {
    width: calc(100% - 20px);
    padding: 0 8px;
    margin: 0 auto 20px;
  }
}

@media (max-width: 600px) {
  .title-wrapper {
    padding: 56px 10px 0;
  }

  .source {
    font-size: 0.7rem;
    text-align: center;
  }
}

@media (max-width: 400px) {
  .content-wrapper {
    width: calc(100% - 20px);
    padding: 0;
    margin: 0 auto 20px;
  }
}


